export interface TextBboxCollection {
  readonly fromBeginning: Rectangle;
  readonly fromLastSaved: Rectangle;
  readonly shop: Rectangle;
  readonly settings: Rectangle;
}

export const JAPANESE_TEXT_BBOX_IN_EXTENDED_HOME_COORDS: TextBboxCollection = {
  fromBeginning: JAPANESE_FROM_BEGINNING_TEXT_BBOX_IN_EXTENDED_HOME_COORDS,
  fromLastSaved: JAPANESE_FROM_LAST_SAVED_TEXT_BBOX_IN_EXTENDED_HOME_COORDS,
  shop: JAPANESE_SHOP_TEXT_BBOX_IN_EXTENDED_HOME_COORDS,
  settings: JAPANESE_SETTINGS_TEXT_BBOX_IN_EXTENDED_HOME_COORDS,
};

export function convertRectangleFromExtendedHomeCoordsToNonextendedHomeCoords(
  extended: Rectangle
): Rectangle {
  const x =
    ((extended.topLeft.x - MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.width) /
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.width) *
    NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.width;

  const y =
    ((extended.topLeft.y -
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.height) /
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.height) *
    NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.height;

  const width =
    (extended.dimensions.width /
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.width) *
    NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.width;

  const height =
    (extended.dimensions.height /
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.height) *
    NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.height;

  return { topLeft: { x, y }, dimensions: { width, height } };
}

export interface PromisableHTMLImageElement extends HTMLImageElement {
  readonly promise: Promise<PromisableHTMLImageElement>;
}

function promisableImage(src: string): PromisableHTMLImageElement {
  const i = new Image() as HTMLImageElement & {
    promise: Promise<PromisableHTMLImageElement>;
  };
  i.src = src;
  i.promise = new Promise((resolve, reject) => {
    i.addEventListener("load", () => resolve(i));
    i.addEventListener("error", reject);
  });
  return i;
}
